import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom'
import { Trigger } from '../client/types'
import CnMStandardModal from '../components/modals/CnMStandardModal'
import { useLogging } from '../context/LoggingContext'
import { useMixpanel } from '../context/MixpanelContext'
import { useVisitor } from '../context/VisitorContext'
import { useCollector } from '../hooks/useCollector'
import { useFingerprint } from '../hooks/useFingerprint'
import { getBrand } from '../utils/brand'
import { hostname, request } from '../utils/http'

type Props = {
  trigger: Trigger
}

const Modal = ({ trigger }: Props) => {
  const { log, error } = useLogging()
  const { resetDisplayTrigger } = useCollector()
  const { trackEvent } = useMixpanel()
  const { appId } = useFingerprint()
  const { visitor } = useVisitor()
  const [open, setOpen] = useState(true)
  const [hasFired, setHasFired] = useState(false)

  const brand = React.useMemo(() => {
    return getBrand()
  }, [])

  useEffect(() => {
    if (!open) return
    if (hasFired) return

    try {
      request
        .put(`${hostname}/triggers/${appId}/${visitor.id}/seen`, {
          seenTriggerIDs: [trigger.id]
        })
        .then(log)
    } catch (e) {
      error(e)
    }

    trackEvent('trigger_displayed', {
      triggerId: trigger.id,
      triggerType: trigger.invocation,
      triggerBehaviour: trigger.behaviour,
      time: new Date().toISOString(),
      brand
    })
    setHasFired(true)
  }, [open])

  if (!open) {
    return null
  }

  const handleClickCallToAction = (e: any) => {
    e.preventDefault()
    trackEvent('user_clicked_button', trigger)
    trigger?.data?.buttonURL && window.open(trigger?.data?.buttonURL, '_self')
  }

  const handleCloseModal = () => {
    trackEvent('user_closed_trigger', trigger)
    resetDisplayTrigger()
    setOpen(false)
  }

  // if (brand === 'C&M')
  return (
    <CnMStandardModal
      trigger={trigger}
      handleClickCallToAction={handleClickCallToAction}
      handleCloseModal={handleCloseModal}
    />
  )
  // if (brand === 'Stonehouse')
  //   return (
  //     <StonehouseModal
  //       trigger={trigger}
  //       handleClickCallToAction={handleClickCallToAction}
  //       handleCloseModal={handleCloseModal}
  //     />
  //   )
  // if (brand === 'Browns')
  //   return (
  //     <BrownsModal
  //       trigger={trigger}
  //       handleClickCallToAction={handleClickCallToAction}
  //       handleCloseModal={handleCloseModal}
  //     />
  //   )

  // return null
}

export const TriggerModal = ({ trigger }: Props) => {
  return ReactDOM.createPortal(<Modal trigger={trigger} />, document.body)
}
